import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import Layout from "@/components/Layout";
import { Button, Form } from "react-bootstrap";
import styles from "../../styles/product.module.css";

const ProductEditPage = () => {
  const [product, setProduct] = useState({
    name: "",
    price: "",
    category: "",
    status: "Active",
    mainImage: "",
    sideImages: [],
    color: "",
    size: "",
    description: "",
    material: "",
  });
  const [isEditing, setIsEditing] = useState(false);
  const router = useRouter();
  const { id } = router.query;  // Get product ID from the URL

  const categories = ["Necklaces", "Bracelets", "Earrings", "Rings", "Watches"]; // Example categories

  useEffect(() => {
    if (id && id !== "add") {
      // Fetch the product details using the `id` for editing.
      // Replace this with your actual fetch logic (e.g., from a database or API).
      const fetchedProduct = {
        id: 1,
        name: "Gold Necklace",
        price: "$100",
        category: "Necklaces",
        status: "Active",
        mainImage: "", // Pre-fill with actual image URL if available
        sideImages: [], // Pre-fill with actual side images
        color: "Gold",
        size: "One Size",
        description: "Elegant gold necklace.",
        material: "Gold",
      };
      setProduct(fetchedProduct);
      setIsEditing(true);
    } else if (id === "add") {
      // Initialize an empty product for adding a new one.
      setProduct({
        name: "",
        price: "",
        category: "",
        status: "Active",
        mainImage: "",
        sideImages: [],
        color: "",
        size: "",
        description: "",
        material: "",
      });
      setIsEditing(false);
    }
  }, [id]);

  const handleFormSubmit = () => {
    if (isEditing) {
      // Handle updating an existing product
      console.log("Updating product:", product);
    } else {
      // Handle adding a new product
      console.log("Adding new product:", product);
    }
    router.push("/product"); // Redirect to the product list after submit
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h1 className={`${styles.pageTitle} text-center mb-4`}>
          {isEditing ? "Edit Product" : "Add Product"}
        </h1>
        <Form>
          {/* Product Name */}
          <Form.Group className="mb-3">
            <Form.Label>Product Name</Form.Label>
            <Form.Control
              type="text"
              value={product.name || ""}
              onChange={(e) =>
                setProduct({ ...product, name: e.target.value })
              }
            />
          </Form.Group>

          {/* SKU */}
          <Form.Group className="mb-3">
            <Form.Label>SKU</Form.Label>
            <Form.Control
              type="text"
              value={product.sku || ""}
              onChange={(e) =>
                setProduct({ ...product, sku: e.target.value })
              }
            />
          </Form.Group>

          {/* Price */}
          <Form.Group className="mb-3">
            <Form.Label>Price</Form.Label>
            <Form.Control
              type="text"
              value={product.price || ""}
              onChange={(e) =>
                setProduct({ ...product, price: e.target.value })
              }
            />
          </Form.Group>

          {/* Category */}
          <Form.Group className="mb-3">
            <Form.Label>Category</Form.Label>
            <Form.Select
              value={product.category || ""}
              onChange={(e) =>
                setProduct({ ...product, category: e.target.value })
              }
            >
              <option value="">Select Category</option>
              {categories.map((category) => (
                <option key={category} value={category}>
                  {category}
                </option>
              ))}
            </Form.Select>
          </Form.Group>

          {/* Status */}
          <Form.Group className="mb-3">
            <Form.Label>Status</Form.Label>
            <Form.Select
              value={product.status || ""}
              onChange={(e) =>
                setProduct({ ...product, status: e.target.value })
              }
            >
              <option value="Active">Active</option>
              <option value="Inactive">Inactive</option>
            </Form.Select>
          </Form.Group>

          {/* Main Image */}
          <Form.Group className="mb-3">
            <Form.Label>Main Image</Form.Label>
            <Form.Control
              type="file"
              onChange={(e) =>
                setProduct({ ...product, mainImage: e.target.files[0] })
              }
            />
          </Form.Group>

          {/* Side Images */}
          <Form.Group className="mb-3">
            <Form.Label>Side Images (Select Multiple)</Form.Label>
            <Form.Control
              type="file"
              multiple
              onChange={(e) =>
                setProduct({
                  ...product,
                  sideImages: [...product.sideImages, ...e.target.files],
                })
              }
            />
            {product.sideImages && product.sideImages.length > 0 && (
              <div className="mt-3">
                <h6>Uploaded Side Images:</h6>
                <ul>
                  {product.sideImages.map((image, index) => (
                    <li key={index}>{image.name}</li>
                  ))}
                </ul>
              </div>
            )}
          </Form.Group>

          {/* Color */}
          <Form.Group className="mb-3">
            <Form.Label>Color</Form.Label>
            <Form.Control
              type="text"
              value={product.color || ""}
              onChange={(e) =>
                setProduct({ ...product, color: e.target.value })
              }
            />
          </Form.Group>

          {/* Size */}
          <Form.Group className="mb-3">
            <Form.Label>Size</Form.Label>
            <Form.Control
              type="text"
              value={product.size || ""}
              onChange={(e) =>
                setProduct({ ...product, size: e.target.value })
              }
            />
          </Form.Group>

          {/* Description */}
          <Form.Group className="mb-3">
            <Form.Label>Description</Form.Label>
            <Form.Control
              as="textarea"
              value={product.description || ""}
              onChange={(e) =>
                setProduct({ ...product, description: e.target.value })
              }
            />
          </Form.Group>

          {/* Material */}
          <Form.Group className="mb-3">
            <Form.Label>Material</Form.Label>
            <Form.Control
              type="text"
              value={product.material || ""}
              onChange={(e) =>
                setProduct({ ...product, material: e.target.value })
              }
            />
          </Form.Group>

          <Button variant="primary" onClick={handleFormSubmit}>
            {isEditing ? "Update Product" : "Add Product"}
          </Button>
        </Form>
      </div>
    </Layout>
  );
};

export default ProductEditPage;
